@import "vars";
@import "layout";



.product-title {
    font-size: 1.5rem;
    color: #fff;
}

.menu-item {
    color: #fff;
    font-size: 0.9rem;
}

.section {  
    .section-header {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto;
        grid-template-areas: 
                            "section-title1"
                            "section-title2"
                            "section-title3";
    
        .section-title1 { grid-area: section-title1; justify-self: center; text-align: center; }
        .section-title2 {  grid-area: section-title2; justify-self: center; min-height: 0;}
        .section-title3 {  grid-area: section-title3; justify-self: center; min-height: 0;}
    }  
    .section-header._has-image {
        display: grid;
        grid-template-columns: min-content min-content max-content min-content;
        grid-template-rows: auto;
        grid-template-areas: 
                            ". section-image section-title1 ."
                            ". . section-title2 section-title2"
                            ". section-title3 section-title3 .";
    
        .section-image { grid-area: section-image; padding-right: 1em; }
        .section-title1 { grid-area: section-title1; justify-self: start; }
        .section-title2 {  grid-area: section-title2; }
        .section-title3 {  grid-area: section-title3; }
    }
}


@media (max-width: $ONE_COL_MAX_WIDTH) {

    .section {
        .section-header {
            &._has-image {
                grid-template-columns: 1fr;
                grid-template-areas:
                    "section-image"
                    "section-title1"
                    "section-title2";
    
                .section-image {
                    padding-right: 0;
                    justify-self: center;
                }
                .section-title1 {
                    justify-self: center;
                    text-align: center;
                }
            }
        }
    }
}

