.section {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    padding: 1em;    

    &.pixodesk-section {
        .small-cards-grid {
            margin-left: 5%;
            margin-right: 5%;
            max-width: calc(min(70em, 100%));
        }
    }
    &.lottie-section {
        padding: 32px 32px 16px 32px;
        // background: #f5f5f7;
        // background: #f8f8f8;
        border-radius: 16px;
        // box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
        background: linear-gradient(90deg, hsl(210, 0%, 95%) 0%, hsl(210, 0%, 100%) 5%, hsl(210, 0%, 100%) 95%, hsl(210, 0%, 95%) 100%);
        // margin-left: 5%;
        // margin-right: 5%;
        max-width: calc(min(70em, 90%));
    
        .small-card {
            border: none;
            background: none;
        }
    }

    .section-header {
        max-width: calc(min(60em, 90%));

        .section-title2, .section-title3 {
            max-width: calc(min(40em, 100%));
            margin-top: 1em;
            margin-bottom: 2em;
        }
    }

    &.icons-section,
    &.splash-section {
        video {
            border-radius: 16px;
            background: #fff;
            // border: 1px solid rgba(0,0,0,0.1);
            // box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
        }
    }
        
    &.icons-section {
        video { 
            padding: 1em;
            box-sizing: border-box;
            width: 30em;
            max-width: 90%;
        }
        // background: #f5f5f7;
        // background: linear-gradient(90deg, hsl(210, 0%, 98%) 0%, hsl(210, 0%, 90%) 50%, hsl(210, 0%, 98%) 100%);
        background: linear-gradient(90deg, hsl(210, 0%, 100%) 0%, hsl(210, 0%, 95%) 50%, hsl(210, 0%, 100%) 100%);
    }
    
    &.loaders-section {
        video {
            max-width: calc(min(30em, 90%));
            // padding: 42px;
            border-radius: 16px;
            background: #fff;
            // border: 1px solid rgba(0, 0, 0, 0.1);
            // box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
        }
    }    

    &.splash-section {
        video {
            padding: 1em;
            box-sizing: border-box;
        }
        // background: #f5f5f7;
        // background: linear-gradient(90deg, hsl(210, 0%, 98%) 0%, hsl(210, 0%, 90%) 50%, hsl(210, 0%, 98%) 100%);
        background: linear-gradient(90deg, hsl(210, 0%, 100%) 0%, hsl(210, 0%, 95%) 50%, hsl(210, 0%, 100%) 100%);
    }    
}

.pricing-section {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-top: 64px;
    margin-bottom: 120px;

    .pricing-section-content {
        display: flex;
        flex-direction: column;
        align-items: start;
    

        .pricing-title {
            margin-top: 3rem;
            margin-bottom: 1rem;
            color: #333;
            ._l1 {
                font-weight: 700;
                font-size: 3rem;
                // line-height: 150% !important;
            }
            ._l2 {
                font-size: 2.5rem;
                font-weight: 400; .strong-text {font-weight: 700;}
                margin-top: 1rem;
                // line-height: 150% !important;
            }
            ._l3 {
                font-size: 1.5rem;
                font-weight: 400; .strong-text {font-weight: 700;}
                margin-top: 1.2rem;
                // line-height: 150% !important;
            }
        }
        .pricing-title2 {
            margin-top: 2rem;
            margin-bottom: 1rem;
            font-size: 0.75rem;
        }
    
        .pricing-features-list {        
            margin-top: 3rem;
            margin-bottom: 4rem;
            max-width: 800px;        
            font-weight: 300;
            font-size: 1rem;
            li {
                line-height: 200%;
            }
        } 
    }      
}
